.customHeader {
    .searchIcon {
        position: absolute;
        left: 0;
        top: 2px;
        width: 26px;
        z-index: 1;

        :global {
            .ms-Button-icon {
                font-size: 15px;
            }
        }
    }

    .searchClose {
        position: absolute;
        right: 0;
        top: 0;
        z-index: 1;

        :global {
            .ms-Button-icon {
                font-size: 15px;
            }
        }
    }

    .clearFilterInput {
        position: absolute;
        right: 33px;
        top: 0;
        z-index: 1;

        :global {
            .ms-Button-icon {
                font-size: 15px;
            }
        }
    }

    .searchQueryDiv {
        position: absolute;
        left: 26px;
        bottom: -11px;
        z-index: 1;

        .searchQuery {
            font-style: italic;
            float: left;
            font-size: 9px;
        }

        .clearFilter {
            color: red;
            float: right;

            :global {
                .ms-Button-icon {
                    font-size: 8px;
                }
            }
        }
    }

    .searchInput {
        max-width: 100%;
    }

    :global {
        .ms-TooltipHost {
            left: 13px;
        }
    }
}