@import '~office-ui-fabric-react/dist/css/fabric.css';

.report {
  display: block;
  width: 100%;
  margin: 10px 0;

  .reportHeaderContainer {
    border-bottom: solid 1px rgba(0, 0, 0, 0.12);

    .reportHeader {
      margin-top: 15px !important;
      margin-bottom: 15px !important;

      .reportTitleLink {
        text-decoration: none;
        cursor: pointer;
      }

      .reportTitleContainer {
        display: table;

        .reportImage {
          display: table-cell;
          vertical-align: middle;
          margin: 5px;
          padding: 0 20px;
          font-size: 25px;
          color: #1565c0;
        }

        .reportTitleDiv {
          margin-left: 30px;
          display: table-cell;
          font-size: 21px;
          font-weight: 600;
          color: #3f3f3f;

          .reportTitle {
            font-size: 20px;
          }

          .reportDescription {
            font-size: 15px;
            color: #a5a5a5;
          }
        }
      }

      .searchInput {
        max-width: 200px;
        float: right;
        margin-right: 15px;
      }

      .columnSelect {
        float: right;
        margin-right: 25px;
        font-size: 26px;

        :global {
          .ms-Dropdown-title {
            border: none;
          }
        }
      }
    }
  }

  .reportBodyContainer {
    padding-top: 10px;
  }

  .reportFutterContainer {
    :global {
      .ms-Grid-row {
        margin-top: 10px;
        margin-bottom: 10px;
      }
    }
  }

  .btnClass {
    float: 'left';
    padding: 0;
    background: 0;
    /* border: 1px solid red; */
    border-radius: 35px;
    min-width: 32px;
  }

  .btnClass:hover {
    background: rgb(244, 244, 244);
  }

  :global {
    .ms-DetailsHeader {
      height: 37px;
    }
  }
}

:global {
  .calloutColumns {
    width: auto;
  }
}